import axios from 'axios'
import { getToken } from './auth';
import { ElMessage } from 'element-plus'
// import store from '@/store'

const service = axios.create({
  // withCredentials: true,
  // baseURL: 'http://127.0.0.1:11002' // dev
  baseURL: 'http://101.200.87.232:11002'
  //  baseURL: 'https://serve.xiongmaojiuxian.eu.org'

})


// 请求拦截器,通过拼接get请求
service.interceptors.request.use(
  config => {
    var res = getToken()
    if (res.token != "") {
      config.headers['token'] = res.token
      config.headers['user_id'] = res.user_id
      config.headers['type'] = res.type
    }
    return config
  },
  error => {
    return Promise.reject(error);
  }
)

service.interceptors.response.use(
  res => {
    console.log(res)
    if (res.data.code === 401) {
      console.log(1233);
      ElMessage({
        message: res.data.msg,
        type: 'error',
        duration: 5 * 1000
      })
      router.push('/login')
    }
    if (res.data.code === 400) {
      ElMessage({
        message: res.data.msg,
        type: 'error',
        duration: 5 * 1000
      })
    }
    else {
      return res
    }
  },
  err => {
    Promise.reject(err)
  }
)

export default service