

<template>
    <div class="container">
        <div class="main">
            <div class="input" v-if="!isregister">
                <div class="title-container">
                    <span class="title">学生登录</span>
                </div>
                <div class="inof">
                    <el-form ref="form" :model="form" label-width="80px">
                        <el-form-item label="邮箱">
                            <el-input v-model="form.account"></el-input>
                        </el-form-item>
                        <el-form-item label="密码">
                            <el-input placeholder="请输入密码" v-model="form.password" show-password></el-input>
                        </el-form-item>

                        <el-form-item v-if="isVerify">
                            <img slot="append" :src="changeImage" @click="handleClickImge" class="codeImage" />
                        </el-form-item>
                        <el-form-item label="验证码" v-if="isVerify">
                            <el-input placeholder="请输入验证码" v-model="form.code"></el-input>
                        </el-form-item>

                        <el-form-item>
                            <el-button type="primary" @click="onSubmit">立即登录</el-button>
                            <el-button type="primary" @click="onRegister">立即注册</el-button>
                        </el-form-item>
                    </el-form>
                </div>
                <!-- 注册 -->
            </div>
            <div class="input" v-if="isregister">
                <div class="title-container">
                    <span class="title">学生注册</span>
                </div>
                <el-form :model="store" class="demo-form-inline">
                    <el-form-item label="头像">
                        <el-upload v-model:file-list="PicList" name="work_file" action="http://127.0.0.1:11002/uploadImage"
                            :show-file-list="false" :on-success="uploadSuccess" :on-remove="handleRemove">
                            <el-avatar :size="70" :src="user.image_url" />
                        </el-upload>
                        <el-dialog v-model="dialogVisible">
                            <img w-full :src="dialogImageUrl" alt="Preview Image" />
                        </el-dialog>
                    </el-form-item>
                    <el-form-item label="学号">
                        <el-input v-model="user.school_student_id" placeholder="请输入学号" />
                    </el-form-item>
                    <el-form-item label="姓名">
                        <el-input v-model="user.name" placeholder="请输入姓名" />
                    </el-form-item>
                    <el-form-item label="班级">
                        <el-select v-model="user.class_id" placeholder="请选择班级" size="large">
                            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" />
                        </el-select>
                    </el-form-item>
                    <el-form-item label="邮箱">
                        <el-input v-model="user.email" placeholder="请输入邮箱" />
                    </el-form-item>
                    <el-form-item label="电话">
                        <el-input v-model="user.phone" placeholder="请输入电话" />
                    </el-form-item>
                    <el-form-item label="密码">
                        <el-input v-model="user.password" placeholder="请输入密码" show-password></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="register">注册</el-button>
                        <el-button type="error" @click="cancel">取消</el-button>
                    </el-form-item>
                </el-form>
                <!-- 注册 -->
            </div>
        </div>
    </div>
</template>
  
<script>
import { getVerify } from '@/api/system/api.js'
import { login, getByToken, class_find, student_create } from '@/api/student/api.js'
export default {
    data() {
        return {
            form: {
                account: '',
                password: '',
                verify_id: '',
                code: '',
            },
            isVerify: false,
            changeImage: "",
            isregister: false,
            user: {
                school_student_id: "",
                name: "",
                class_id: "",
                image_url: "",
                email: "",
                phone: "",
            },
            options: [],

        }
    },
    created() {
        this.getOptions()
        this.handleClickImge()
    },
    methods: {
        getUserMessage() {
            getByToken().then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    sessionStorage.setItem("name", res.data.data.name)
                    sessionStorage.setItem("image_url", res.data.data.image_url)
                }
            })
        },
        uploadSuccess(file, fileList) {
            this.user.image_url = file.data
            this.$message({
                showClose: true,
                message: "上传成功",
                type: "success",
            })
        },
        async getOptions() {
            const res = await class_find()
            if (res.data.code === 200) {
                if (res.data.data) {
                    var value = "";
                    var label = "";
                    for (let index = 0; index < res.data.data.data.length; index++) {
                        value = res.data.data.data[index]._id
                        label = res.data.data.data[index].name
                        this.options.push({
                            value: value,
                            label: label,
                        })
                    }
                } else {
                    this.options = []
                }
            }
        },
        // 获取验证码
        handleClickImge() {
            getVerify().then(res => {
                this.changeImage = res.data.data.base64
                this.form.verify_id = res.data.data.verify_id

            })
        },
        onSubmit() {
            login(this.form).then(res => {
                if (res.data.code != 200) {
                    if (res.data.data >= 3) {
                        `1`
                        this.isVerify = true
                        this.handleClickImge()
                    } else {
                        this.isVerify = false
                    }
                    this.$message.error(res.data.msg);
                } else {
                    sessionStorage.setItem('token', res.data.data.token)
                    sessionStorage.setItem('user_id', res.data.data.user_id)
                    this.getUserMessage()
                    this.$message({
                        showClose: true,
                        message: "登录成功",
                        type: "success",
                    })
                    setTimeout(() => {
                        this.$router.push('/student/home').catch(() => { })
                    }, 100);

                }
            })
        },
        onRegister() {
            this.isregister = true
        },
        cancel() {
            this.isregister = false
        },
        register() {
            student_create(this.user).then(res => {
                if (res.data.code != 200) {
                    this.$message.error(res.data.msg);
                } else {
                    this.$message({
                        showClose: true,
                        message: "注册成功",
                        type: "success",
                    })
                    this.user = {};
                    this.isregister = false
                }
            })
        },
    }
}
</script>
  
<style>
.container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.main {
    /* display: flex; */
    position: absolute;
    top: 50%;
    left: 50%;
    height: 70%;
    width: 27%;
    min-height: 300px;
    /* 设置最小高度 */
    min-width: 300px;
    /* 设置最小宽度 */

    transform: translate(-50%, -50%);
    box-shadow: 0 1px 12px #00000054;
}

.input {
    padding: 5%
}

.inof {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, 30%);
    padding: 5%
}

.title-container {
    height: 50px;
}

.title {
    display: flex;
    /* position: relative; */
    justify-content: center;
    /* 水平居中 */
    align-items: center;
    /* 垂直居中 */

    font-weight: bolder;
    font-size: larger;
}
</style>
  