import request from '@/utils/request'

export function login(data) {
    return request({
        url: '/student/login',
        method: 'post',
        data
    })
}


// 注册
export function student_create(data) {
    return request({
        url: '/student/student/create',
        method: 'post',
        data
    })
}
// 学生获取信息
export function getByToken() {
    return request({
        url: '/student/getByToken',
        method: 'get',
        params: {},
        data: {}
    })
}
// 学生获取数据展示
export function getDashboard() {
    return request({
        url: '/dashboard/student',
        method: 'get',
        params: {},
        data: {}
    })
}

// 获取课程列表
export function course_find_student(data) {
    return request({
        url: '/work/course/find/student',
        method: 'get',
        params: data,
        data: {},
    })
}


// 加入课程
export function course_join(data) {
    return request({
        url: '/work/course/join',
        method: 'post',
        params: {},
        data: data,
    })
}

// 退出课程
export function course_exit(data) {
    return request({
        url: '/work/course/exitByStudent',
        method: 'delete',
        params: data,
        data: {},
    })
}
